import React, { Component } from 'react';
import Squeegie from './components/Squeegie';

class App extends Component {
  currActive = 0;

  componentDidMount(){
    this.slides = document.querySelectorAll("section");
    this.navItems = document.querySelectorAll(".nav-item");

    this.addFadeUps();

    document.addEventListener("orientationchange", function(event){
      switch(window.orientation) 
      {  
          case -90: case 90:
              break; 
              
          default:
      }
  });
  }

  addFadeUps(){
    this.fadeUps = [];

    this.slides.forEach(slide => {
      slide.querySelectorAll(".fadeUp").forEach((fadeUp, i) => {
        fadeUp.style.transitionDelay = 0.5 * (i + 1) + "s";
      })
    })
  }

  next(e){
    // if(e.target.classList.contains("nav-item")){
    //   return;
    // }

    setTimeout(() => {
      this.slides[this.currActive].classList.remove('-active');

      if(this.currActive === this.slides.length - 1){
        this.currActive = 0;
      } else {
        this.currActive++;
      }

      this.slides[this.currActive].classList.add('-active');
    }, 400)
  }

  previous(){
    this.slides[this.currActive].classList.remove('-active');

    if(this.currActive === 0){
      this.currActive = this.slides.length - 1;
    } else {
      this.currActive--;
    }

    this.slides[this.currActive].classList.add('-active');
  }

  goTo(goTo){
    setTimeout(() => {
      this.slides[this.currActive].classList.remove('-active');
      this.currActive = goTo;
      this.slides[this.currActive].classList.add('-active');

      document.querySelectorAll("video").forEach(video => {
        if(video.getAttribute("muted") !== null) return;

        let parent = video.parentElement,
            clone = video.cloneNode();
  
        clone.setAttribute("muted","");
        video.remove();
        parent.append(clone);
      })
    }, 400);
  }

  render() {
    return (
      <main>
        {/* <section className="slide-1 -active" style={{backgroundImage: `url(./images/images/picture1.jpg)`}}></section>
        <section className="slide-2" style={{backgroundImage: `url(./images/images/picture2.jpg)`}}></section> */}
        <section className="nav -active">
          <ul className="nav-list">
            <li className="nav-item" onClick={() => this.goTo(1)}>Reserve</li>
            <li className="nav-item" onClick={() => this.goTo(2)}>Angus</li>
            <li className="nav-item">Cervena</li>
            <li className="nav-item">NZ Beef</li>
            <li className="nav-item">NZ Venison</li>
            <li className="nav-item">NZ Lamb</li>
          </ul>
        </section>
        <section className="meat -reserve  -active" onClick={() => this.goTo(0)}>
          <div className="meat-background"></div>
          <img src="./images/logo-reserve.png" alt="" className="meat-logo fadeUp"/>
          <div className="meat-top">
            <Squeegie 
              data={{classes: "meat-top-squeegie", img1: './images/SFF_Slide_Video-01.jpg', img2: './images/SFF_Slide_Video-02.jpg'}}
              expandable={true}
            />
          </div>
          <div className="meat-bottom">
            <div className="meat-bottom-left">
              <h1 className="meat-bottom-left-title fadeUp">The Premier Selection</h1>
              <img src="./images/meat-2.jpg" alt="" className="meat-bottom-left-img"/>
            </div>
            <div className="meat-bottom-right fadeUp">
              <p className="meat-bottom-right-paragraph">Extraordinary eating quality, guaranteed.</p>
              <p className="meat-bottom-right-paragraph">Not all beef is created equal. Silver Fern Farms Reserve is an elite standard of grass-fed beef. Our promise of consistent tenderness, juiciness and flavour is backed by our Eating Quality System. Our Master Graders hand-select the best based on proven scientific criteria.</p>
              <p className="meat-bottom-right-paragraph">A truly exclusive range, this is the top 4% of New Zealand grass-fed beef.</p>
            </div>
          </div>
        </section>
        <section className="meat -angus" onClick={() => this.goTo(0)}>
          <div className="meat-background"></div>
          <img src="./images/logo-angus.png" alt="" className="meat-logo fadeUp"/>
          <div className="meat-top">
            <video src="./videos/video-1.mp4" className="meat-top-video" autoPlay loop/>
          </div>
          <div className="meat-bottom">
            <div className="meat-bottom-left">
              <h1 className="meat-bottom-left-title fadeUp">The boldest beef flavour</h1>
              <video src="./videos/video-2.mp4" className="meat-bottom-left-video" autoPlay loop/>
            </div>
            <div className="meat-bottom-right fadeUp">
              <p className="meat-bottom-right-paragraph">Full-bodied and powerful; the true taste of Angus beef.</p>
              <p className="meat-bottom-right-paragraph">Silver Fern Farms Angus brings the perfect combination of flavour, texture and juiciness with every cut. Natural, with grass-fed marbling, the distinctive boldness of Angus beef at its best.</p>
              <p className="meat-bottom-right-paragraph">The Angus breed brought to its bold best in New Zealand by the skill of our farmers and the quality of our wide-open pastures.</p>
            </div>
          </div>
        </section>
      </main>
    );
  }
}

export default App;



